.v3 {
    .sidebar {
        .track-information {
            .youtube-info.synth-info {
                .synth-thumbnail.sound-thumbnail {
                    margin-left: 20px !important;
                    margin-right: 20px !important;
                    border-radius: 0px;
                    display: flex;
                    padding: 0px !important;
                    position: relative;
                    width: unset;
                    
                    
                    
                    .white-key {
                        background-color: white;
                        border: 1px solid black;
                        border: 1px solid #0000001f;
                        height: 15px;
                        min-height: 15px;
                        width: 8px;
                    }
                    
                    .black-key {
                        background-color: black;
                        position: absolute;
                        width: 5px;
                        height: 8px;
                    }
                    
                    .key-1 {
                        left: 5px;
                        top: 0px;
                    }
                    .key-2 {
                        left: 14px;
                        top: 0px;
                    }
                }
            }
        }
        
        .object-explorer {
            .track-option {
                .youtube-info.synth-info {
                    padding: 0px !important;
                    .synth-thumbnail.sound-thumbnail {
                        display: flex;
                        padding: 0px !important;
                        position: relative;

                        .white-key {
                            background-color: white;
                            border: 1px solid black;
                            border: 1px solid #0000001f;
                            height: 15px;
                            min-height: 15px;
                            width: 3px;
                        }

                        .black-key {
                            background-color: black;
                            position: absolute;
                            width: 2px;
                            height: 8px;
                        }

                        .key-1 {
                            left: 2px;
                            top: 0px;
                        }
                        .key-2 {
                            left: 5px;
                            top: 0px;
                        }
                    }
                }
            }
        }
    }
}
