.year-exploring {
    .source-samples {
        height: 100% !important;
    }
    .year {
        display: flex;
        font-size: 14pt;
        color: #ffffff87;;
        

        .year-chooser {
            margin-left: 8px;

            .arrow-down, .arrow-up {
                cursor: pointer;
            }
            
            .arrow-down {
                border-left-width: 5px;
                border-right-width: 5px;
                border-top-width: 10px;
                border-top-color: white;
            }
            .arrow-up {
                width: 0;
                height: 0;
                margin-top: 3px;
                margin-left: 2px;
                border-left: 5px solid transparent;
                border-right: 5px solid transparent;
                border-bottom: 10px solid white;
            }
        }
    }
}
.source-exploring, .year-exploring {
    position: absolute;
    border-bottom-left-radius: 5px;
    border-top-left-radius: 5px;
    position: absolute;
    z-index: 20000000000000;
    width: 400px;
    height: 400px;
    background-color: #3837379e;
    backdrop-filter: blur(4px);
    padding: 10px;
    display: flex;
    flex-direction: column;

    .clear-btn {
        position: absolute;
        top: 5px;
        cursor: pointer;
        font-size: 10pt;
        background-color: white;
        color: black;
        border: 2px solid black;
        border-radius: 12px;
        height: 15px;
        width: 15px;
        line-height: 8px;
        text-align: center;
        right: 5px;
    }

    .styles {
        span {
            font-size: 8pt;
            margin-right: 3px;
        }
    }
    
    .source-samples {
        height: 240px;
        background-color: #0000009e;
        overflow-y: scroll;
        overflow-x: hidden;
    }
   
    .sample-stream-option {
        font-size: 10pt;
    }

    .source-top {
        display: flex;
        .cover-art {
            background-size: 100% 100%;
            width: 150px;
            height: 150px;
        }

        .source-info {
            padding: 10px;
            font-size: 12pt;
            white-space: nowrap;
            overflow: hidden;

            .release-year {
                color: #ffffff87;;
                cursor: pointer;
            }

            .release-year:hover {
                background-color: white;
            }
        }

    }
}

svg.visual-link {
    top: 0px;
    position: absolute;
    width: 100%;
    z-index: 100000;
    height: 100%;
    pointer-events: none;
    
    polygon {
        z-index: 20000;
        fill: url(#MyGradient);
        backdrop-filter: blur(4px);
    }
}
