
.hide.sounds-pane {
    .track-quick-view {
        .steps-dropdown.btn-group {
            margin: 0px;
            margin-top: 0px;
            margin-left: 0px;
            margin-right: 0px;
            margin-bottom: 0px;

            .dropdown-menu {
                min-width: unset;
                width: 100px;
                padding: 5px;
            }

            .dropdown-item {
                padding: 2px;
                font-size: 8pt;

                .group-checkmark {
                    height: 20px;
                    margin-left: 5px;
                }
            }

            .btn-primary {
                border: none;
                box-shadow: 0px 0px 2px 2px white;
                background-color: white !important;
                color: black;
                padding: 2px;
                padding-left: 5px;
                font-size: 8pt;
            }
        }
    }
}


.step-group-editor {
    margin-top: 5px;
    border-top: 1px solid #ffffff1a;
    padding: 5px;



    .Dropdown-menu {
        z-index: 10000;
    }

    .effect.selected {
        box-shadow: unset;
    }

    .effect {
        margin: 0px;
        width: 233px;
        float: none;
        .effect-header {
            display: none;
            float: none;
        }

        .user-script-component {
            .script-box {
                width: 100%;
                height: 120px;
            }
        }
    }

    .transformation.none {
        input.when-value, .unit-type {
            opacity: 0.1;
            pointer-events: none;
        }
    }

    .new-transform-btn {
        background-color: white;
        color: black;
        width: 15px;
        height: 15px;
        border-radius: 15px;
        line-height: 10px;
        text-align: center;
        cursor: pointer;
        margin-left: auto;
    }

    input.then-value.when-value {
        width: 65px;
    }

    input.when-value {
        height: 20px;
        width: 40px;
        background-color: black;
        color: white;
        font-size: 8pt;
        outline: none !important;
        border: none;
        margin-left: 5px;
        margin-right: 5px;
    }

    .transformation {
        display: flex;


        input.mapping {
            cursor: pointer;
            animation: fadeparam 1s 0.1s infinite;
            animation-direction: alternate;
        }

        .parameter-box {
            display: flex;
            font-size: 8pt;
            width: 100%;

            .parameter-label-container {
                flex: 1;
            }
            
            .slider.slider-horizontal {
                margin-top: 5px;
                margin-left: auto;
                width: 100px;
            }
        }


        .unit-type {
            background-color: black;
            color: white;
            font-size: 8pt;
            line-height: 20px;
            min-width: 20px;
            text-align: center;
        }

        .action.Dropdown-root {
            margin-left: 5px;
        }

        .Dropdown-control {
            background-color: #00000052 !important;
        }
        
        .Dropdown-control, .Dropdown-option {
            width: 80px;
            height: 20px;
            line-height: 13px;
            border: none;
            font-size: 8pt;
            padding: 3px;
        }

        .Dropdown-arrow {
            top: 6px;
        }
    }
    .group-header {
        display: flex;
        
        .group-thumb {
            width: 18px;
            height: 18px;
            margin-right: 5px;
        }

        .group-name {
            background-color: black;
            color: white;
            margin-right: 5px;
            padding: 2px;
            font-size: 8pt;
        }
    }
}
