.new-selector {
    min-height: 600px;

    .wave-and-name {
        width: 100%;
    }

    .progress-bar {
        position: relative !important;
        transform: unset;
        left: 5px !important;
        top: 20px !important;
        margin-left: 10px;
        width: 300px !important;
        height: 30px !important;
        margin-top: 10px;
        background-color: #0c71d6 !important;

        .loading-text {
            font-size: 12pt !important;
            line-height: 15px !important;
        }

        .inner-progress-bar, .progress {
            height: 28px !important;
        }

        .progress {
            background-image: linear-gradient(to right, #5e5ec5, #21e3f1) !important;
            margin-left: -4px;
        }
    }
    
    .dot:hover {
        box-shadow: 0px 0px 1px 1px white;
    }

    ::-webkit-scrollbar-thumb {
        background: #e7e4e43b !important;
        opacity: 0.2 !important;
    }

    .search-and-back {
        display: flex;
    }

    .selector-inner {
        display: flex;
        position: relative;

        .unsourced-header {
            font-size: 14pt;
            padding: 4px;
        }
        .artist-flow {
            .artist-flow-input {
                width: 380px;
                border-color: #022b9f94;
                height: 30px;
                color: white;
                background-color: #060f38a8;
            }

            .artist-save-button {
                background-color: #00ffad;
                color: black;
                padding: 3px;
                text-align: center;
                cursor: pointer;
            }
        }
    }

    .source-to-search-results {
        margin-top: 50px;
        overflow-y: scroll;
    }

    .day-results {
        width: 100%;
        height: 600px;
        padding-top: 50px;
        overflow-y: scroll;
        overflow-x: hidden;
    }

    .source-results {
        display: flex;
    }

    .source-results.alone {
        padding-top: 50px;
        width: 100%;
        height: 600px;
        overflow-y: scroll;
    }

    .source-results.odd {
        // background-color: #2dffe21f;
        background-color: #04011526;
    }

    .selector-back-button {
        width: 20px;
        cursor: pointer;
        border: solid white;
        border-width: 0 6px 6px 0;
        display: inline-block;
        padding: 3px;
        height: 20px;
        margin-top: 5px;
        margin-left: 5px;
        transform: rotate(135deg);
    }
    
    .list-results.showing-source {
        margin-top: 30px;
    }

    .list-results {
        /*
        overflow-y: scroll;
        overflow-x: hidden;
        */
        width: 500px;

        .source-release-name {
            display: flex;
            letter-spacing: 1px;
            white-space: nowrap;
            overflow: hidden;
            background-color: #00000094;
            backdrop-filter: blur(4px);
            width: 100%;
            color: white;
            padding: 4px;
            position: absolute;
            z-index: 100;
            top: 30px;

            .pack-thumb {
                width: 15px;
                height: 15px;
                margin-right: 10px;
            }
        }
    }

    .day-date {
        color: white;
        padding: 3px;
        background-color: #092f9a78;
        background-image: linear-gradient(45deg, transparent, #fffdfd2b);
    }

    .sound-grid.hide {
        display: none;
    }

    .sound-grid {
        display: flex;
        padding-top: 60px;
        flex-wrap: wrap;
        overflow-y: scroll;
        overflow-x: hidden;
        align-items:flex-start;
        align-content:flex-start;

        /*
        .day:hover {
            opacity: 1 !important;
        }
        */

        .day>* {
            flex: 1 1 auto;
          }

        .day.toggled {
            .floppy-inner {
            }

            .floppy-part {
                margin-right: auto;
                display: flex;
                margin-left: 0px;
                border-top-right-radius: 15px;

                .date {
                    background-color: #2d2d2d0f!important;
                    color: #fff;
                    margin: 2px 24px 2px 4px;
                }
            }

            .sources {
                display: flex;
                border-top-right-radius: 15px;
                border-bottom-right-radius: 15px;
                border-bottom-left-radius: 15px;
                overflow: hidden;
            }
        }

        .day.current {
            opacity: 1 !important;
            .floppy-part {
                background-color: white;
                .date {
                    background-color: unset;
                    color: black;
                }
            }
        }

        .day {
            margin-right: 6px;
            margin-top: 10px;
            background-color: transparent !important;
            cursor: pointer;
            // border-radius: 4px;
            display: flex;
            flex-direction: column;

            .floppy-inner {
                width: 90%;
                /* height: 1px; */
                margin-left: auto;
                margin-right: auto;
                filter: saturate(0.5);
            }

            .floppy-part {
                background-color: black;
                /* margin-right: 2px; */
                margin-left: 2px;
                display: flex;
                /* border-bottom-right-radius: 4px; */
                /* border-bottom-left-radius: 4px;*/

                .date {
                    padding-left: 6px;
                    padding-right: 6px;
                    color: white;
                    background-color: black;
                    font-size: 10pt;
                    white-space: nowrap;
                    overflow: hidden;
                    letter-spacing: 1px;
                    margin: 10px 10px 10px auto;
                }
            }

            .sources {
                display: none;
                flex-wrap: wrap;
                font-size: 7pt;
                padding: 5px;
                background-color: #101010bd;

                .source-container.bigger {
                    background-color: #5f5f5f12;
                }

                .source-container {
                    display: flex;
                    width: 380px;
                    height: 38px;
                    padding: 2px;
                    border: 1px solid #0000000f;

                    svg {
                        margin-left: auto;
                        margin-top: auto;
                        margin-bottom: auto;
                    }

                    .source-title {
                        color: #fff;
                        font-size: 9pt;
                        letter-spacing: 1px;
                        line-height: 30px;
                        overflow: hidden;
                        word-break: break-all;
                    }

                    .book-right {
                        margin-left: auto;
                        font-size: 6pt;
                        line-height: 30px;
                        margin-right: 10px;
                    }

                    .book-left, .book-right {
                        color: #bbbbbb;
                    }

                    .book-left {
                        text-orientation: sideways-right;
                        writing-mode: vertical-rl;
                        padding-left: 0px;
                        margin-right: 9px;
                        line-height: 12px;
                        min-width: 10px;
                        max-width: 10px;
                        font-size: 9pt;
                        cursor: pointer;
                        // background-color: #00000091 !important;
                        background-size: cover;
                        word-break: break-all;
                        overflow: hidden;
                    }
                    
                    .source-thumb {
                        margin-left: auto;
                        padding-left: 3px;
                        text-orientation: sideways-right;
                        writing-mode: vertical-lr;
                        width: 45px;
                        min-width: 45px;
                        line-height: 12px;
                        font-size: 9pt;
                        cursor: pointer;
                        background-size: cover;
                        color: black;
                        word-break: break-word;
                        overflow: hidden;

                        span {
                        }
                    }
                    text-align: center;

                    .bar {
                        display: none;
                        width: 55px;
                        height: 6px;
                        background-color: black;

                        .bar-length {
                            position: absolute;
                            color: red;
                            font-size: 6pt;
                        }

                        .bar-indicator {
                            top: 0px;
                            left: 15px;
                            height: 6px;
                            background-color: white;
                            font-size: 6pt;
                        }
                    }
                }
            }
        }
    }

    .source-results {
        .waveform-and-rating {
            display: flex;
            position: relative;

            .rating {
                margin-top: 10px;
                font-size: 8pt;
                flex: 1;
                margin-left: 15px;
            }
        }

        .sound-waveform {
            border: none;
            max-width: 326px;
        }
        
        .unsourced.source-results-thumb {
            width: 100%;
            max-width: 40px;
            border: none;
            height: unset;
            font-size: 14pt;
            white-space: nowrap;
            writing-mode: vertical-rl;
            text-orientation: mixed;
            text-align: center;
            // background-color: #ffffff14 !important;
            color: black;
            overflow: hidden;

            span {
                line-height: 35px;
            }
        }

        .source-results-thumb {
            background-image: linear-gradient(45deg, #baa8a8, transparent);
            border-radius: 15px;
            font-size: 12pt;
            padding-top: 15px;
            padding-bottom: 15px;
            cursor: pointer;
            border: 1px solid #3700b3;
            width: 40px;
            min-width: 40px;
            background-size: 100% 100%;
            word-break: break-word;
            color: white;
        }

        .sample-options {
            .release-name {
                letter-spacing: 1px;
                background-color: #00000045;
                padding: 4px;
                color: #b9b7b7;
                font-size: 11pt;
            }
        }

        .sample-option.current {
            background-color: #ffffff6e;


            .sample-year {
                color: white !important;
            }

            .tag-option {
                background-color: #484848;
            }
        }

        .sample-option {
            width: 370px;
            padding: 3px;
            position: relative;

            .guild-name {
                font-size: 8pt;
                margin-left: 3px;
                margin-right: 3px;
                background-color: #ffffff29;
                line-height: 20px;
                border-radius: 2px;
                padding-left: 5px;
                padding-right: 5px;
                color: white;
            }

            .pack-indicators {
                display: flex;
                margin-top: 5px;
                margin-right: 5px;
                
                .pack-thumb {
                    width: 10px;
                    height: 10px;
                }
            }

            .heart {
                margin-top: 5px;
                margin-left: 5px;
                width: 10px;
                height: 18px;
            }
            .heart:after {
                left: 0px;
            }
            .heart:before {
                left: 5px;
            }
            .heart:before, .heart:after {
                top: 0;
                width: 5px;
                height: 10px;
                border-radius: 5px 5px 0 0;
            }

            .tags-info {

            }

            .sample-option-inner {
                display: flex;
            }

            .projects-used:hover {
                opacity: 0.9;
            }

            .projects-used {
                cursor: pointer;
                font-size: 8pt;
                background-color: #464646;
                opacity: 0.7;
                color: white;
                padding-left: 2px;
                padding-right: 2px;
                border-radius: 5px;

                span {
                }
            }

            .name-and-tags {
                display: flex;
                max-width: 365px;

                .sample-name {
                    padding: 3px;
                    margin-left: 4px;
                    flex: 1;
                    overflow: hidden;
                    white-space: nowrap;
                    cursor: pointer;
                    font-size: 9pt;
                    line-height: 15px;
                }

                .instrument-type {
                    margin-left: auto;
                    background-color: #363346;
                    color: black;
                    padding-left: 3px;
                    padding-right: 3px;
                    font-size: 9pt;
                    overflow: hidden;
                    white-space: nowrap;
                }

                .sample-year {
                    font-size: 8pt;
                    margin-left: 5px;
                    color: #a2a2a2;
                }

                .sample-tags {
                    margin-top: 3px;
                    max-width: 150px;
                    display: flex;
                    overflow: hidden;

                    .tag-option:hover {
                        filter: brightness(1.5);
                    }
                    
                    .tag-option {
                        cursor: pointer;
                        margin-left: 2px;
                        color: white;
                        padding-left: 3px;
                        padding-right: 3px;
                        font-size: 8pt;
                        overflow: hidden;
                        white-space: nowrap;
                    }
                }
            }

            .dot.unloaded {
                display: none;
                background-color: unset !important;
            }
            .dot {
                margin-top: 3px;
                margin-left: 4px;
            }
        }
    }

    .entire-search-container {
        display: flex;
        z-index: 10;
        position: fixed;
        backdrop-filter: blur(4px);
        backface-visibility: hidden;
        top: 57px;
        left: 0px;
        width: 100%;

        .search-rest {
            flex: 1;
        }

        .search-container {
            width: 98%;
        }

        .filter-options {
            display: flex;
            .heart {
                margin-left: 5px;
                flex: 1;
                margin-top: 3px;
            }
        }

        .Dropdown-menu {
            width: 100px !important;
            right: 0px;
        }

        .Dropdown-control {
            margin-left: auto;
            height: 20px;
            width: 70px;
            padding: 2px;
            padding-left: 8px;
            font-size: 9pt;
            border-color: #28282b;
            background-color: #041594 !important;
            
            .Dropdown-arrow {
                top: 7px;
            }
        }
    }
    .search-container {
        position: relative;
        display: flex;


        img {
            width: 28px;
            height: 28px;
            margin-left: 2px;
            opacity: 0.8;
            cursor: pointer;
            filter: brightness(0.7);
        }

        img.disabled {
            pointer-events: none;
            opacity: 0.1;
        }
        img.selected {
            box-shadow: 0px 0px 2px 2px #b3bef7;
            filter: saturate(3);
        }

        .search-clear-button {
            position: absolute;
            right: 70px;
            cursor: pointer;
            top: 0px;
        }

        width: 100%;

        .source-to-search {
            width: 30px;
            height: 30px;
            opacity: 1;
            box-shadow: 0px 0px 4px 4px #ffffff69;
            background-size: cover;
            cursor: pointer;
            position: absolute;
            right: 90px;
            filter: unset;
            z-index: 10000;
        }

        #sample-search-input {
            width: 100%;
            border-color: #022b9f94;
            height: 30px;
            color: white;
            background-color: #1f2b62a8;
        }
    }

}


.new-selector.presets {
    .results-toolbar {
        display: flex;
        padding: 3px;
        background-color: #092f9a78;
        background-image: linear-gradient(45deg, transparent, #fffdfd2b);

        .settings-info {
            margin-right: 5px;
        }

        .preset-setting {
            margin-right: 5px;
            background-color: #2b2742ad;
            padding-left: 3px;
            padding-right: 3px;
            cursor: pointer;
        }

        .preset-setting.selected {
            background-color: white;
            color: black;
            box-shadow: 0px 0px 2px 2px #636161;
        }
    }


    .preset-options {
        width: 100%;

        .sample-option {
            min-height: unset;
            display: flex;
        }
    }
}

.new-selector.sequences {
    .search-container {
        #sample-search-input {
            width: 99% !important;
        }
    }
    .search-options {
        display: flex;
        float: none;
        width: 97%;
        background-color: #1a1a1ac7;
        backdrop-filter: blur(4px);
        /* margin-top: 50px; */
        position: fixed;
        padding-top: 5px;
        top: 76px;
        z-index: 1000;
        
        .heart:before, .heart:after {
        }
        
        .favorited.heart:before, .favorited.heart:after {
            background: #fc2e5a !important;
        }

        .heart {
            filter: contrast(2);
            margin-top: 8px;
            margin-right: 5px;

        }

        .star {
            margin-top: 0px;
        }

        .search-option{
            margin-right: 5px;
            background-color: #2b2742ad;
            padding-left: 3px;
            padding-right: 3px;
            cursor: pointer;
        }

        .search-option.selected {
            background-color: white;
            color: black;
            box-shadow: 0px 0px 2px 2px #636161;
        }
    }
    .sequences-list.has-expanded-project {
        padding-top: 20px;
    }

    .sequences-list {
        overflow-y: scroll;
        padding-top: 72px;

        .work {
            margin: 0px;
        }
        
        .project-and-back {
            display: flex;

            .project-option {
                flex: 1;
            }
        }

        .project-edits {
            margin-left: 30px;

            .project-option {
                background-color: #1b00ff40;
            }
        }

        .project-option {
            position: relative;
            height: 32px;
            font-size: 9pt;
            background-color: #2b2b506e;
            margin-bottom: 2px;
            padding: 5px;
            display: flex;

            .heart {
                margin-top: 0px;
                margin-left: 5px;
                margin-right: 0px;
                transform: scale(0.65);
            }

            .star {
                margin-left: 5px;
            }

            .local-only {
                margin-left: 5px;
                margin-right: 5px;
                background-color: #0037ff;
                padding-left: 3px;
                padding-right: 3px;
            }

            .dot {
                margin-top: 5px;
                margin-right: 5px;
            }

            .project-name {
                cursor: pointer;
               line-height: 20px;
            }

            .edits-btn.showing {
                box-shadow: 0px 0px 2px 2px #454a64;
                background-color: white;
                color: black;
            }

            .edits-btn {
               color: #8888ff;
               cursor: pointer;
               padding-left: 5px;
               line-height: 20px;
               padding-right: 5px;
               margin-left: 10px;
               padding-left: 5px;
               background-color: #29292f; 
            }

            .project-date {
                margin-left: auto;
                margin-right: 5px;
                line-height: 20px;
                padding-left: 5px;
                padding-right: 5px;
                background-color: #ffffff2b;
                color: #ffffffa3;
            }

            .dot {
                border-style: solid;
                background-color: unset;
            }

            .private-project {
                color: #ff172c;
                padding-left: 5px;
                padding-right: 5px;
                margin-right: 5px;
            }

            .project-guild {
                padding-left: 5px;
                background-color: black;
                padding-right: 5px;
                margin-right: 3px;
                max-width: 200px;
                overflow: hidden;
            }

            .project-author {
                line-height: 20px;
                color: black;
                padding-left: 5px;
                padding-right: 5px;
            }
        }

        .expanded-project {
            // padding-left: 10px;
            padding-right: 5px;
            padding-top: 3px;
            display: flex;
            flex-direction: column;

            .expanded-toolbar {
                display: flex;
            }

            .load-project-btn:active {
                box-shadow: 0px 0px 2px 2px white;
            }

            .load-project-btn {
                margin-left: auto;
                padding-left: 3px;
                padding-right: 3px;
                font-size: 9pt;
                padding-top: 1px;
                padding-bottom: 1px;
                color: black;
                background-color: white;
                cursor: pointer
            }

            .subsequence-buttons {
                display: flex;
                margin-left: 5px;

                .mini-subsequence-btn {
                    background-color: #454545;
                    color: white;
                    width: 15px;
                    height: 15px;
                    font-size: 8pt;
                    text-align:center;
                    line-height: 15px;
                    margin-right: 3px;
                    cursor: pointer;
                }

                .mini-subsequence-btn.current {
                    background-color: white;
                    color: gray;
                }
            }

            .pattern-numbers {
                display: flex;

                .mini-pattern-btn {
                    background-color: #454545;
                    color: white;
                    width: 15px;
                    height: 15px;
                    font-size: 8pt;
                    text-align:center;
                    line-height: 15px;
                    margin-right: 3px;
                    cursor: pointer;
                }

                .mini-pattern-btn.current {
                    background-color: white;
                    color: gray;
                }
            }

            .sub {
                position: relative;
                background-color: #4a4a4a6e;
                padding: 2px;
                display: flex;
                flex-direction: column;

                .load-module {
                    position: absolute;
                    top: 0px;
                    left: 0px;
                    cursor: pointer;
                    background-color: white;
                    color: black;
                    font-size: 9pt;
                    padding-left: 3px;
                    padding-right: 3px;
                    padding-top: 1px;

                    .module-name {
                        margin-left: 5px;
                        background-color: black;
                        color: white;
                        padding-left: 2px;
                        padding-right: 2px;
                    }
                }
                .load-btn {
                    background-color: white;
                    color: black;
                    font-size: 9pt;
                    padding-left: 3px;
                    padding-right: 3px;
                    padding-top: 1px;
                    padding-bottom: 1px;
                    cursor: pointer;
                    margin-left: auto;
                }
                
                .mini-track {
                    display: flex;
                    margin: 5px;
                    padding: 2px;
                    font-size: 8pt;

                    .mini-thumbnail {
                        width: 15px;
                        height: 15px;
                        background-size: cover;
                        cursor: pointer;
                    }

                    .mini-thumbnail.selected {
                        box-shadow: 0px 0px 4px 4px #d7d7d7;
                    }

                    .track-name {
                        color: white;
                        padding: 0px;
                        font-size: 8pt;
                        margin-left: 5px;
                        background-color: unset;
                        width: 80px;
                        cursor: unset;
                    }

                    .track-steps {
                        display: flex;
                        .track-step {
                            width: 10px;
                            height: 7px;
                            margin-left: 3px;
                            background-color: black;
                        }
                    }
                }
            }
        }
    }
}

.presets.new-selector, .new-selector.sequences {
    .search-container {
        .search-clear-button {
            right: 10px;
        }
    }

    .search-options {
        margin-top: 10px;
    }

    .sample-option {
        display: flex;
        height: unset;
        min-height: unset;
    }
}

.sample-dropdown {
    width: 200px;
    position: absolute;
    background-color: #272727c7;
    z-index: 1000;
    color: white;
    padding: 5px;
    backdrop-filter: blur(4px);
    padding-left: 15px;
    border-radius: 5px;

    .menu-item:hover {
        background-color: white;
        color: black;
        position: relative;
        .packs {
            max-height: 200px;
            overflow: scroll;
            width: 180px;
            display: block;
            position: absolute;
            left: 70px;
            top: 10px;

            .menu-item {
                display: flex;

            }
            .new-pack {
                border-bottom: 1px solid #ffffff2e;
                margin-bottom: 5px;
                color: #00ff95
            }
        }
    }

    .packs-included-in {
        margin-top: 5px;
        border-top: 1px solid #ffffff2e;
        padding-top: 5px;
    }
    .menu-item {
        font-size: 10pt;
        display: flex;
        padding: 3px;

        .pack-thumb {
            width: 10px;
            height: 10px;
            margin-right: 5px;
        }
        
        .heart {
            margin-top: 2px;
            margin-left: 5px;
        }

        .arrow-right {
            border-top: 5px solid transparent;
            border-bottom: 5px solid transparent;
            border-left: 12px solid #a2a2a2;
            margin-top: 5px;
            margin-left: 5px;
        }

        .packs {
            display: none;
        }
    }
}


