
.folder-modal {
    .folder-actions {
        margin-top: 10px;
        display: flex;
    }
}

.preset-folders {
    .header {
        font-size: 10pt;
        background-color: #242635;
        height: 30px;
        padding: 5px;
        border-bottom: solid 2px #0b212c;
    }
    background-color: #181924;
    clear: both;
    border: solid 2px #0b212c;
}

.folders-container {
    clear: both;
    
    .folder-name.selected {
        background-color: #3b3a61 !important;
    }

    .folder {
        padding-top: 2px;

        .folder-name.hover {
            background-color: #3b3a61 !important;
        }

        .folder-name {
            padding: 5px;

            .name-label {
                flex: 1;
            }

            .collapse-button {
                padding-left: 5px;
                padding-right: 5px;
                padding-bottom: 5px;
                margin-right: 5px;
            }

            font-size: 11pt;
            line-height: 11px;
            display: flex;
        }

        .folder-item.selected {
            background-color: #3b3a61 !important;

            .item-icon {
                border-color: black;
                background-color: black;
            }

            .item-icon:hover {
                background-color: white;
            }
        }

        .folder-item {
            padding-top: 2px;
            font-size: 10pt;
            display: flex;
            margin-left: 10px;
            line-height: 20px;
            color: #9ca4a9;

            .item-icon {
                margin-top: 5px;
                border-color: white;
                border-style: solid;
                border-width: thin;
                width: 10px;
                height: 10px;
                margin-right: 8px;
            }
        }
    }
}
