.v3 {
    .right.sidebar {
        .zobject-operations {
            padding-top: 10px !important;
            border-top: 1px solid var(--dividerColor);
            margin-top: 10px;
            display: flex;
            flex-direction: column;
            .label {
                text-align: unset;
                font-weight: unset;
                font-size: 10pt;
                padding-bottom: 10px !important;
            }
        }
    }
}
.operation-type {
    .name-and-description {
        display: flex;
        .operation-name {
            margin-right: 5px;
        }

        .operation-description {
            color: #777777;
        }
    }

    .operation-args {
        .operation-param {
            display: flex;
            margin-left: 10px;

            .dot {
                width: 8px;
                height: 8px;
                min-width: 8px;
                min-height: 8px;
                border-radius:12px;
                margin-right: 5px;
            }

            .param-name {
                margin-right: 5px;
            }
            .param-description {
                color: #777777;
            }

        }
    }

}
