
#password {
    background-color: black;
    color: white;
    height: 45px;
    border-color: black;
}

.password-container {
    a {
        background-color: #333333 !important;
        color: #f4215e !important;
    }
}

.input-group.selector-search {
    width: 300px;
}

.main-area {
    #search-input, .search-input {
        color: white;
        background-color: #050512;
        border-color: #092434;
    }
}

.filters-pane {
}

.filter {
    float: left;
}

.chord-pane {
    padding: 5px;
    clear: both;
    border-style: solid;
    border-width: 1px;
    margin-bottom: 4px;
    border-color: rgba(66, 211, 241, 0.43);
    height: 100px;
}

.chord-title {
    padding-left: 5px;
}

.notes {
    float: left;
}

.note-input-container {
    float: left;
}

.year-pane {
    clear: both;
}

.year-slider-container {
    float: left;
}

.styles-pane {
    .styles-container {
        max-height: 200px;
        overflow: scroll;
    }
}

.countries-pane, .styles-pane {
    clear: both;
}

.filter.show {
    background-color: red;
}

.pagination-pane {
    display: flex;
    flex-direction: row;
    width: 100%;
}

.field-pagination-button:hover {
    background-color: #100e0e
}

.field-pagination-button {
    width: 100%;
    flex-grow: 2;
    padding: 2px;
    text-align: center;
    float: left;
    cursor: pointer;
    border-width: thin;
    border-style: solid;
    border-color: #092535;
    background-color: #121212;
}

.search-results {
   clear: both;
   margin-top: 5px;
   margin-bottom: 40px;
   min-height: 400px;

   .sound-waveform {
       border-color: #65080e;
   }

}

.youtube-info {
    float: left;
}

.add-sample-button {
    float: left;
    width: 70px;
    line-height: 30px;
    text-align: center;
    font-size: 45pt;
    padding: 5px;
    float: left;
    color: #42f4f1;
    cursor: pointer;
}

.layer-button {
    margin-top : 20px;
    margin-left: 10px !important;
}

.sound-buttons {
    float: left;
    text-align: center;
}
.pagination-pane {
    clear: both;
}

.pagination-start {
    float: right;
    padding: 5px;
}

.pagination-total {
    float: right;
    text-align: center;
    padding: 5px;
}

.pagination-prev{
    float: left;
    cursor: pointer;
    border-style: solid;
    border-width: thin;
    border-color: #42f4f1;
    padding: 5px;
}

.pagination-next {
    float: left;
    border-style: solid;
    border-color: #42f4f1;
    border-width: thin;
    cursor: pointer;
    padding: 5px;
}

.search-results-header {
    font-size: 12pt;
    line-height: 8px;
    flex-growth: 2;
    margin-top: 15px;
    margin-left: 5px;
    margin-bottom: 5px;
    float: left;
}

.filter-favorites-button {
    float: right;
    margin-right: 10px;
}

.pagination-container {
    width: 100%;
}

.btn-primary,
.btn-primary:hover,
.btn-primary:active,
.btn-primary:visited,
.btn-primary:focus {
    background-color: #082435 !important;
    border-color: #082435 !important;
}

.search-box-container {
    float: left;

    .badge-light {
        font-size: 9pt !important;
    }
}

.filter-dropdown-container {
    float: left;
}

.huge {
    font-size: 30pt !important;
    line-height: 30px;
}

.tag.selected.and {
    color: #ff638f;
    opacity: 1 !important;
                
}

.tag:hover {
    opacity: 1 !important;
}

.tag.selected.or {
    color: #85a9ff;
}

.medium {
    font-size: 25px;
}

.videos-container {
    display: flex;
    flex-direction: row;
    overflow: scroll;
    margin-bottom: 5px;
}

.video-option-container.odd {
    background-color: #1f1e1e;
}

.video-option-container.selected:hover {
    height: 60px;
    width: 60px;
    margin-top: 5px;

    .video-option {
        height: 60px;
        width: 60px;
    }
}

.video-option-container.selected {
    height: 60px;
    width: 60px;

    border: none;

    .video-option {
        cursor: pointer;
    }
}

.video-option-container:hover {
    width: 100px;
    height: 100px;
    margin-top: 0px;
    .video-option {
        width: 100px;
        height: 100px;
    }

    .video-title {
        display: block;
    }
}

.videos-filter{
    background-color: #1e1f22;
    height: 130px;

    .selected-video-info {
        border: 1px solid #1f245f;
        width: 350px;
        background-color: #01010f;

        .title-and-year {
            display: flex;
        }

        .video-title {
            padding: 3px;
            max-width: 320px;
            background-color: #3100a2;
            font-size: 9pt;
        }
        .video-year {
            margin-left: auto;
            padding: 3px;
            font-size: 8pt;
            background-color: #5700d9;
        }
    }
}

.video-option-container {
    display: flex;
    margin-top: 5px;
    border: 1px solid #0a202e;
    margin-right: 0px;
    height: 90px;
    width: 20 px;
    .video-title {
        display: none;
        font-size: 6pt;
    }

    .video-option {
        cursor: pointer;
        object-fit: none;
        width: 20px;
    }

    .video-option-info {
        margin-top: 5px;
        font-size: 9pt;
        margin-left: 10px;
        .video-title {
            font-size: 10pt;
            background-color: #373738;
            color: #f8f8f9;
            padding: 2px;

            .samples-count {
                margin-left: 10px;
                color: #87a3e2;
                font-size: 9pt;
                margin-right: 10px;
            }
        }
        
        .video-tags {
        }

        .video-styles {
        }

        .video-year {
        }
    }
}

.video-option.selected-video {
    border-style: solid;
    border-color: #5700d8;
    width: 60px;
    height: 60px;
}

.tags-sound-wrapper {
    display: flex;
    max-width: 320px;
}

.tag-sound-button {
    float: left;
    color: #42f4f1;
    font-size: 14pt;
    cursor: pointer;
    margin-left: 5px;
}


.tag-sound-input {
    float: left;
    margin-top: 5px;
}

.tag-sound-input-box {
    height: 24px !important;
    font-size: 10pt !important;
    width: 40px;
    background-color: black !important;
    color: white !important;
}

.filter-option {
    cursor: pointer;
    margin-left: 5px;
    margin-top: 5px;
    margin-bottom: 15px;
}

.tags-pane {
    clear: both;
    overflow: scroll;
    max-height: 200px;
}

.filter-header {
    margin-bottom: 10px;
}

.option-tabs {
    display: flex;
    align-items: center;
    width: 100%;
    margin-left: 10px;
    margin-bottom: 5px;
}

.option-tab {
    margin-top: 8px;
    font-size: 10pt;
    margin-right: 5px;
    margin-left: 10px;
    cursor: pointer;
    color: #c2c3c5;
}

.option-tab:hover {
    background: -webkit-linear-gradient(#eee, #e7fbff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
    opacity: 40px;
}

.option-tab-underline {
    border-bottom-style: solid;
    border-width: 2px;
    width: 30px;
    margin: auto;
    text-align: center;
}

.option-tab.selected {
    background: -webkit-linear-gradient(#eee, #e7fbff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
    opacity: 40px;
}

.page-size-chooser-container {
    float: right;
    margin-right: 10px;

    .Dropdown-control {
        height: 25px !important;
        line-height: 10px;
        margin-top: 10px;
        font-size: 8pt;

        .Dropdown-arrow {
            top: 10px;
        }
    }
}


.downloading-sample {
    width: 325px;
    height: 50px;
    border-color: #651212;
    border-width: 1px;
    border-style: solid;
    color: #e12159;
    padding-top: 5px;
    background-color: black;
}

.date-sampled-pane {
    clear: both;
    padding: 10px;
    color: #a7a8a8;
    border-style: solid;
    border-width: 1px;
    border-color: #062434;
    background-color: #1f1e1e;
    font-size: 10pt;
    
    .filter-header {
        font-size: 14pt;
        color: white;
        margin-bottom: 10px;
    }
    
    .date-dropdown-container {
        display: flex;
        
        .Dropdown-root {
            margin-right: 20px;
            
            .Dropdown-control {
                height: 20px;
                line-height: 5px;
            }
        }
    }
}

.samples-2d {
    clear: both;
    height: 500px;
    width: 500px;
}

.email-address-input {
    color: white !important;
    background-color: black !important;
    height: 30px !important;
    width: 320px !important;
}

.sample-identifier {
    color: red;
    background-color: black;
    padding: 5px;
}

.dmca-reasoning-input {
    background-color: black !important;
    color: white !important;
}

.search-options {
    display: flex;
    float: right;

    .Dropdown-control {
        border: 1px solid #292828;
    }
}

.playlist-row {
    .sound-and-tags {
        float: left;
        border-right: 3px solid #410c0b;
        max-width: 337px;
    }

    .sound-thumbnail.big, .big.youtube-thumbnail {
        width: 78px !important;
        max-width: 78px !important;
        height: 78px !important;
        margin-right: 10px;
    }

    .big.youtube-thumbnail {
        border: 1px solid #3102a3 !important;
    }
}

.playlist-row.odd {
    background-color: #1e1f21;
    border-top: solid 2px #161718;
    border-bottom: solid 2px #161718;
}

.sample-name {
    margin-top: 3px;
    flex: 1;
    font-size: 8pt;
    .name-text {
        padding: 3px;
        max-width: 200px;
        background-color: #323232;
        border: 1px solid #201a1a;
    }
}

.empty-video {
    width: 15px;
    height: 90px;
    opacity: 0;
}


.link-icon {
    margin-left: 5px;
    margin-right: 3px;
    margin-top: 3px;
    cursor: pointer;
    width: 15px;
    height: 15px;

}

.name-text.guild-name-text {
    background-color: #6000f9;
    border-radius: 3px;
}

