.recently-added-sounds {
    overflow: scroll;
    background-color: var(--explorerColor);
    padding: 8px;

    .recent-sound.selected {
        background-color: white;
        color: black;
    }
    
    .recent-sound {
        padding: 3px;
        font-size: 10px;

        display: flex;

        .recent-thumb {
            width: 12px;
            height: 12px;
            border-radius: 12px;
            margin-right: 10px;
            background-image: radial-gradient(at top right, #e5afaf, transparent);
        }
    }
}
