
.module-loader.needs-click {
    width: 150px;
    height: 100px;
    cursor: pointer;
    background-color: #80808036;
}
.module-loader.new-module {
    margin-top: 50px;
}

.module-loader {
    margin-top: 10px;
    width: 250px;
    height: 200px;
    margin-left: 20px;
    display: flex;
    flex-direction: column;

    .click-me {
        margin: auto;
    }

    .module-list {
        margin: 2px;
        flex: 1;
        overflow: scroll;

        .module-option {
            .basic {
                display: flex;
            }

            .expansion  {
            }

            margin-bottom: 1px;
            background-color: #252525;
            font-size: 8pt;
            padding: 4px;
            .module-name {
                
            }

            .module-types {
                display: flex;
                margin-left: 10px;
                .module-name {
                    cursor: pointer;
                    text-align: center;
                    width: 12px;
                    height: 12px;
                    border-radius: 12px;
                    line-height: 12px;
                    background-color: #C4C4C4;
                    color: black;
                    margin-right: 5px;
                }
            }
        }
    }
    input {
        background-color: black;
        color: white;
        width: 100%;
        font-size: 10pt;
    }
}
