.higher-order-component {
    background-color: #000000d1;
    align-items: flex-start;
    margin-bottom: 30px;
    display: flex;

    .higher-order-header {
        width: 100%;
        display: flex;
        padding: 5px;
        
        .name {
            margin-left: 5px;
            margin-right: 5px;
        }

        .collapse-container {
            margin-left: auto;
            cursor: pointer;
        }

        .arrow-down {
            border-left: 6px solid transparent;
            border-right: 6px solid transparent;
            border-top: 14px solid white;
        }
        .arrow-left {
            cursor: pointer;
            width: 0;
            height: 0;
            border-top: 6px solid transparent;
            border-bottom: 6px solid transparent;
            border-right: 12px solid white;
            margin-top: 6px;
            margin-left: 2px;
        }
    }

    .higher-sidebar {
        flex: 1;
        border-left: 1px dashed #ffffff42;

        .higher-order-parameters {
            .parameter-box {
                padding: 10px;
                font-size: 9pt;
                .slider.slider-horizontal {
                    width: 150px;
                }
            }
        }

        .higher-order-header {
            .name {
                display: none;
            }
        }

        .inner-higher-sidebar {
            display: flex;
        }
        .higher-order-component {
            margin-bottom: 0px;
        }

        .orientation-routing {
            margin-right: 10px;
            margin-left: 10px;
            font-size: 8pt;
            display: flex;
            flex-direction: column;

            .in-label, .out-label {
                font-size: 6pt;
                color: #636363;
            }

            .in-label {
                margin-top: 10px;
                text-align: center;
                writing-mode:  vertical-lr;
                margin-right: 5px;
            }

            .out-label {
                padding-left: 30px;
                text-align: center;
            }

            .m-row {
                display: flex;
            }
            .module-outs, .m-routing {
                display: flex;
            }

            .module-outs {
                padding-left: 36px;
                .out-line {
                    margin-left: 3px;
                    height: 6px;
                    width: 1px;
                    background-color: white;
                }

                .m-name {
                    width: 15px;
                    margin-right: 4px;
                }
            }

            .m-routing {
                .m-name {
                    width: 9px;
                }

                .in-line {
                    margin-top: 8px;
                    width: 6px;
                    height: 1px;
                    background-color: white;
                    margin-right: 3px;
                }

                .m-option.connected {
                    background-color: white;
                }

                .m-option {
                    width: 15px;
                    margin-top: 3px;
                    margin-right: 9px;
                    cursor: pointer;
                    border: 1px solid white; 
                    width: 10px;
                    height: 10px;
                    border-radius: 10px;
                }
            }
        }

        svg.orientation-routing {
            margin-left: 10px;
            width: 100px;

            path.selected, path:hover {
                stroke: red;
            }

            polygon {
                fill: white;
            }

            path {
                stroke-width: 2px;
                fill: transparent;
            }
            
            rect:hover, circle:hover {
                fill: red;
            }
            
            text {
                pointer-events: none;
                font-size: 7pt;
            }
        }

        .module-scripter {
            display: flex;
            .user-script-component {
                pre {
                    min-height: 150px;
                }
                .script-box {
                    height: 150px;
                    background-color: #1f1f23a1 !important;
                }
            }
            
            .module-router {
                display: flex;
                background-color: #ffffff38;

                .module-routes {
                    margin: 10px;
                    img {
                        cursor: pointer;
                        margin-top: 10px;
                        margin-bottom: 10px;
                        width: 15px;
                        margin-left: 5px;
                        margin-right: 5px;
                    }
                    .module-indicator {
                        background-color: black;
                        width: 25px;
                        text-align: center;
                        line-height: 25px;
                        height: 25px;
                        border-radius: 100px;
                        color: white;
                    }
                }
                
            }
        }
        .algorithm-chooser {
            display: flex;
            padding-left: 20px;
            padding-top: 20px;

            .algorithm-choices {
                display: flex;
                flex: 1;
                align-items: flex-start;
                flex-wrap: wrap;


                .algorithm-choice.chosen {
                    background-color: white;
                    color: black;
                }

                .algorithm-choice {
                    cursor: pointer;
                    align-items: flex-start;
                    background-color: #52525247;
                    font-size: 9pt;
                    color: white;
                    padding: 3px;
                    margin: 5px;
                }
            }
            
            .triangle-icon {
                border-width: 64px !important;
                width: 0px;
                height: 0px;
            }
            margin-bottom: 20px;
        }
    }
    .higher-inner {
        display: flex;
        position: relative;
        .higher-playhead {
            background-image: linear-gradient(
                352deg
                , #ffffff1a, transparent);
            z-index: 100000;
            pointer-events: none;
            position: absolute;
            width: 32px;
            height: 320px;
            background-color: #d4d4d49a;
        }

        svg.selection {
            z-index: unset;
        }

        svg.moving {
            polygon:active {
                pointer-events: none;
            }
        }

        svg {
            z-index: 100;
            pointer-events: none;
            position: absolute;
            left: 0px;
            top: 0px;
            width: 512px;
            height: 320px;

            path {
                stroke: #ececec1f;
            }

            polygon.selected, polygon:hover {
                stroke: #fbfbfb;
                stroke-width: 3px;
                stroke-dasharray: 7,8px;
            }



            polygon {
                pointer-events: auto;
                fill: white;
            }
        }

        .higher-order-step.on.orientation-0 {
            border-left: 4vw solid white;
            border-top: 4vw solid transparent;
        }
        .higher-order-step.on.orientation-1 {
            border-right: 4vw solid white;
            border-bottom: 4vw solid transparent;
        }
        .higher-order-step.on.context {
            z-index: 100000000;
        }
        .higher-order-step.on {
            width: 0;
            height: 0;
            z-index: 1;
            position: absolute;

            .context-menu {
                .menu-item {
                    display: flex;
                    .color-btn {
                        margin-left: auto;
                        width: 15px;
                        height: 15px;
                        border-radius: 15px;
                        background-color: white;
                    }
                }
            }
        }

        .higher-col.even {
            .higher-step {
                background-color: #4e4e4e0d;
            }
            .higher-step.even {
                background-color: transparent;
            }
        }

        .higher-col.dragging {
            z-index: 4;
        }

        .higher-col {
            display: flex;
            flex-direction: column;
            z-index: 1;

            .higher-step.even {
                background-color: #4e4e4e52;
            }
            .higher-step {
                background-color: transparent;
            }

            .higher-step.selection {
                background-color: red !important;
                opacity: 0.5;
            }

            .higher-step.selected {
                // box-shadow: 0px 0px 2px 2px white;
            }


            .higher-step:hover {
                border: 1px solid white;
            }

            .higher-step {
                position: relative;
                cursor: pointer;
                width: 32px;
                height: 32px;
            }
        }
    }
}

.higher-sidebar {
    /*
    .macro-map-button {
        font-size: 12pt;
        margin-left: auto;
        margin-right: auto;
        padding: 5px;
        margin-top: 20px;
    }
    */

    .parameter-searcher {
        font-size: 9pt;
        width: 180px;
        position: relative;

        .delete-btn {
            position: absolute;
            cursor: pointer;
            font-size: 12px;
            top: 0px;
            right: 5px;
            color: white;
            z-index: 1000;
        }

        .create-macro-btn:active {
            box-shadow: 0px 0px 2px 2px white;
        }

        .create-macro-btn {
            cursor: pointer;
            font-size: 12pt;
            background-color: white;
            color: black;
            padding: 3px;
            text-align: center;
        }
    }
    input {
        width: 100%;
        color: white;
        background-color: #29292969;
    }
    .parameter-results {
        max-height: 300px;
        overflow-y: scroll;
        .parameter-result.selected {
            background-color: white;
            color: black;
        }
        .parameter-result {
            display: flex;
            font-size: 8pt;
            padding: 3px;
            overflow: hidden;
            white-space: nowrap;
            cursor: pointer;

            
            .p-sub-device {
                color: black;
            }
            .p-track {
                color: black;
            }
            .p-sub-sequence {
                width: 15px;
                height: 15px;
                min-width: 15px;
                border-radius: 15px;
                color: black;
                text-align: center;
            }

            .p-name, .p-track, .p-sub-sequence, .p-effect, .p-sub-device {
                padding-left: 2px;
                padding-right: 2px;
                margin-right: 3px;
            }
        }
    }
}
