.arrangement-view {
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
    -khtml-user-select: none; /* Konqueror HTML */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; /* Non-prefixed version, currently
                          supported by Chrome and Opera */      
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
    flex: 1;

    .arrangement-bottom-container {

        .automation-toolbar {
            display: flex;
            background-color: #3c3b42e3;
            background-image: unset;

            .mode {
                display: none;
                
            }

            .rest-toolbar {
                flex: 1;
            }
        }

        width: 100%;
        z-index: 8;
        border: 1px solid #252525;
   }

    .arrangement-resizing-edge {
        width: 100%;
        height: 10px;
        cursor: row-resize;
        background-color: #17281d;
        opacity: 0.3;
        
    }

    .full-arrangement-container {
        display: flex;
        width: 100%;
        height: 100%;
        overflow-y: scroll;
        // overflow: scroll;

        .side-panel-container {
            background-color: #0b0e0d70;
            position: relative;
            padding-top: 50px;

            .side-panel-toolbar {
                position: absolute;
                background-color: #3c3c3c;
                width: 100%;
                z-index: 1000;

                .actions-btn {
                    width: 15px;
                    height: 15px;
                }

                .btn-primary {
                    background-color: #d0d0d01f !important;
                    border: none;
                    padding: 4px;
                    font-size: 9pt;
                    margin-bottom: 23px;
                    color: black;
                }
            }
        }

        .arrangement-container {
            overflow-y: hidden;
            overflow-x: scroll;
        }

        .viewport-wrapper {
            position: relative;
            display: flex;
            flex-direction: column;
            height: 100%;
        }
    }

    .arrangement-rest {
        flex: 1;
    }

    .selection-container {
        z-index: 1000;
        height: 24px;
        position: absolute;
        padding-right: 100px;

        .selection {
            height: 12px;
            border: 1px solid gray;
            background-color: #292929b0;
            z-index: 4;
            display: flex;
            z-index: 999;

            .triangle-right {
                width: 0;
                height: 0;
                border-top: 5px solid transparent;
                border-left: 10px solid #686d6a;
                border-bottom: 5px solid transparent;
                cursor: w-resize;
                z-index: 1500;
            }

            .middle {
                flex: 1;
                cursor: col-resize;
                z-index: 1500;
            }

            .triangle-left {
                width: 0;
                height: 0;
                border-top: 5px solid transparent;
                border-right: 10px solid #686d6a;
                border-bottom: 5px solid transparent;
                cursor: e-resize;
                z-index: 1500;
            }
        }
        
        .selection.looping {
            background-color: #686d6a;
            .triangle-left {
                border-right-color: #120a0a;
            }
            .triangle-right {
                border-left-color: #120a0a;
            }
        }
    }

    .clock-ticker {
        position: absolute;
        margin-top: 2px;
        height: 100%;
        width: 2px;
        background-color: red;
        opacity: 0.8;
        z-index: 10;
    }

    .mouse-helper {
        position: absolute;
        height: 58px;
        width: 2px;
        background-color: orange;
        opacity: 0.1;
        z-index: 9;
    }

    .time-info {
        height: 25px;
        width: 140px;
    }

    .pattern-visualization {
        position: relative;
        margin-top: 2px;
        .track-steps {
            position: relative;
            
            .track-step {
                position: absolute;
            }
        }
    }

    .pattern-info {
        border-right: 8px solid white;
        font-size: 12pt;
        line-height: 60px;
        width: 140px;
        height: 65px;
        background-color: #3c3c3c;
        display: flex;
        flex-direction: column;

        .panel-rest {
            display: flex;
            margin-top: auto;
            margin-bottom: 5px;
            flex-direction: column;
            padding-right: 10px;

            .track-controls-container {
                display: flex;
                margin-top: auto;

                .tracks-show-button {
                    margin-left: auto;
                    width: 18px;
                    height: 18px;
                    border-radius: 18px;
                    background-color: #3e3e3e0a;
                    cursor: pointer;

                    .arrow-down {
                        margin-top: 4px;
                        margin-left: 4px;
                        border-left: 5px solid transparent;
                        border-right: 5px solid transparent;
                        border-top: 10px solid black;
                    }

                    .arrow-right { 
                        border-top: 5px solid transparent;
                        border-bottom: 5px solid transparent;
                        border-left: 10px solid black;
                        margin-top: 3px;
                        margin-left: 5px;
                    }
                }

                .volume-box {
                    margin-top: 0px;
                    width: 60px;
                    height: 20px;
                    line-height: 20px;
                    border: none;

                    .volume-indicator {
                        height: 20px;
                        background-color: #f7f7f7e3 !important;
                    }
                }
            }
        }

        .pattern-name {
            flex: 1;
        }
    }

    .arrangement-column {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 100%;

        .sub-sequence-container {
            display: flex;
            flex-direction: column;
            margin-top: 50px;
        }
        
        .pattern-row.rolls-row {
            margin-bottom: 0px;
            flex-direction: row;
        }

        .pattern-row.selected {
            opacity: 1;
            background-color: #ffffff73;
        }

        .pattern-filler {
            height: 60px;
        }

        .pattern-row {
            display: flex;
            flex-direction: column;
            height: 65px !important;
            border-top-style: solid;
            border-top-width: 1px;
            border-top-color: #393737;
            border-bottom-style: solid;
            border-bottom-width: 1px;
            border-bottom-color: #393737;
            background-color: #ffffff45;
            z-index: 3;
            white-space: pre; // Key line
            opacity: 0.9;

            .patterns-arrangement-container {
                display: flex;
            }

            .empty-roll-event {
                display: flex;
            }

            .roll-event {
                display: flex;
                z-index: 8;

                .pattern.last {
                    flex-basis: 0;
                }
            }
            
            .pattern.selected {
            }

            .small-pattern.pattern {
                height:20px;
            }

            .pattern {
                position: relative;
                flex-wrap: nowrap;
                flex-shrink: 2;
                font-size: 8pt;
                display: flex;
                flex-direction: column;
                border-style: solid;
                border-width: 1px;
                border-color: #303138;
                text-align: center;
                color: black;
                cursor: pointer;
                word-wrap: break-word;

                .pattern-top {
                    background-image: linear-gradient(90deg, transparent 50%,#ffffff12 50%);
                    backgroudn-size: 0px 50px;
                    width: 100%;
                    height: 20px;
                    z-index: 8;
                    display: flex;
                    overflow: hidden;

                    .center {
                        flex: 1;
                    }
                    .left-edge {
                        cursor: col-resize;
                        height: 100%;
                        width: 12px;
                    }
                    .right-edge {
                        cursor: col-resize;
                        height: 100%;
                        width: 12px;
                        margin-left: 0px !important;
                    }
 
                }

                .pattern-rest {
                    height: 42px;
                    display: flex;
                    background-color: #0d0718bf;
                }

                .pattern-number {
                    flex: 1 ;
                    width: 100%;
                    line-height: 18px;
                    font-size: 10pt;
                }
           }
        }
        
        .time-background {
            left: 100px;
            position: absolute !important;
            z-index: 0;
            opacity: 0.2;

            .bar {
                height: 50px !important;
            }
        }

        .time-row.bottom {
            .bar {
                border-top: 1px solid #363636;
            }
            
            margin-top: auto;
        }

        .time-row:active {
            // z-index: 1;
        }
        
        .time-row, .time-background {
            will-change: top;
            backface-visibility: hidden;
            position: absolute;
            // z-index: 250;
            // backoground-color: black;

            .mouse-helper.flashit {
                opacity: 1;
                animation: none !important;
                height: 10px;
                z-index: 10000;
                top: 15px;
                background: unset;
                border-left: 6px #d6917700 solid;
                border-right: 6px transparent solid;
                border-top: 6px #f59a12 solid;
            }
        
            .time-full-background {
                background-color: #3c3c3c;
                position: absolute;
                z-index: 1000;
                width: 100%;
                height: 25px;
            }

            .time-top-background {
                background-color: #3c3c3cc2;
                position: absolute;
                z-index: 1000;
                width: 100%;
                top: 25px;
                height: 25px;
            }

            .inner-time-row {
                display: flex;
                // cursor: zoom-in;
            }

            .bar-filler {
                height: 25px;
            }
            
            .bar {
                font-size: 8pt;
                height: 25px;
                line-height: 10px;
                
                display: flex;

                .beat.hidden {
                    border-left-color: #44444426;
                    opacity: 0.4;
                }

                .beat {
                    margin-top: auto;
                    padding-top: 2px;
                    padding-left: 2px;
                    flex-basis: 25%;
                    flex-grow: 0;
                    height: 750px;
                    z-index: 0;
                    border-left-style: solid;
                    border-left-width: 1px;
                    border-left-color: #444444;
                }
                
                .beat-number {
                    z-index: 1000;
                    position: absolute;
                    top: 0px;
                    border-left: solid #ffffff21 1px;
                    height: 50px;
                    padding-top: 3px;
                    padding-left: 3px;
                }

                .first-beat {
                    position: relative;

                    flex-basis: 25%;
                    flex-grow: 0;
                    margin-top: auto;
                    z-index: 0;
                    height: 750px;
                    border-left-style: solid;
                    border-left-width: 1px;
                    border-left-color: #fbfbfd73;
                }

                .beat.selected, .first-beat.selected {
                    background-color: orange;
                }
            }
        }
    }
    .pattern-info {
        .sub-sequence-name {
            font-size: 9pt;
            padding-left: 11px;
            line-height: 20px;
            color: black;
            // background-color: #0a0f0e;
            cursor: pointer;
        }

        .sub-sequence-name.roll {
            color: white;
        }
    }

    .pattern-info.selected {
        .sub-sequence-name {
            // background-color: #ffffff1f !important;
        }
    }

    .pattern-info.sub-sequence {
        padding: 0px;
        line-height: 30px;
        
        .option-and-label {
            display: flex;
            flex-direction: column;
       }
        
        .automation-type-dropdown {
            line-height: 2px;
            width: 85px;

            .automation-type-control-dropdown {
                font-size: 9pt;
                border-style: none !important;
                border-radius: 0px;
                height: 20px;
                line-height: 3px;
            }

            .Dropdown-control {
                background-color: #ffffff !important;
                color: black !important;
            }

            .Dropdown-arrow {
                top: 6px !important;
            }

            .Dropdown-option {
                height: 30px;
                line-height: 10px;
                font-size: 9pt;
            }
        }
    }

    .pattern-row.automation {
        position: relative;
    }

    .absolute-selection {
        position: absolute;
        height: 0px;
        z-index: 0;
        z-index: 1;
    }

    .absolute-selection.looping {
        height: 100%;
        background-color: #025669;
        opacity: 0.8;
        border: 1px solid red;
    }

    .row-selection {
        position: absolute;
        height: 65px;
        background-color: red;
        z-index: 2;
        opacity: 0.5;
    }

    .linear-automation-container {
        z-index: 10;
        position: absolute;
        top: 20px;

        svg {
            background-color: #000000a1;
        }
    }

    .side-browser-container {
        min-width: 300px;
        background: linear-gradient(#1b201c, #000 40%);
        overflow-y: scroll;

        .options {
            display: flex;

            .option {
                padding: 3px;
                font-size: 8pt;
                margin-right: 5px;
                cursor: pointer;
                border-right: 1px solid white;
                border-bottom: 1px solid white;
            }

            .option.selected {
                border-right: none;
                border-bottom: none;
                border-left: 1px solid white;
                border-top: 1px solid white;
            }

        }

        .xanadu-folders {
        }

        .browser-search {
            color: white;
            background-color: black;
            width: 100%;
        }

        .preset-folders {
            background-color: #000000;
            clear: both;
            border: solid 2px #252525;

            .folders-container .folder-name.selected {
                background-color: #133a23 !important;
            }
        }
    }

    .midi-recording-button {
        margin-left: 5px;
        height: 12px;

        .plain-dot {
            margin-bottom: 12px;
        }
    }

    .mini-piano-roll-wrapper {
        display: flex;
        flex-direction: column;
        width: 100%;
        position: absolute;
        height: 65px;
        
        .mini-piano-roll-header {
            height: 16px;
            width: 100%;
            z-index: 50;
            display: flex;

            .middle {
                flex: 1;
            }
        }

        .mini-piano-roll.steps-map {
            border: none;
            width: 100%;
            height: 65px;
            position: relative;
        }
    }

    .piano-roll-container {
        padding-top: 0px;

        .piano-roll-wrapper {
            border: none;


            .time-grid-step {
                border-color: #151b287a;
            }

            .time-grid-step.bar {
                border-left: 2px solid #14134161;
            }

            .grid-steps-container {
                border-bottom: 1px solid #06090e29;
            }

            .time-grid-step.current-step {
                border-left-color: #b41919;
            }

            .grid-step {
                z-index: 100;
            }
        }
    }


    ::-webkit-scrollbar-thumb {
        background: white;
        -webkit-border-radius: 1ex;
        -webkit-box-shadow: 0px 1px 2px #141414;
    }

 
}


.tracks-side-panel {
    // border-right: 8px solid white;

    .track-side-panel {
        margin-left: 8px;
        display: flex;
        flex-direction: column;
        background-color: #3c3c3ce8;
        border-right: 8px solid white;

        .track-controls {
            display: flex;
            flex-direction: row;
            margin-right: 0px;
            margin-top: auto;
            margin-bottom: 3px;

            .solo-btn, .mute-btn, .enable-btn {
                cursor: pointer;
                margin-left: 3px;
                font-size: 8pt;
                line-height: 13px;
                color: black;
                text-align: center;
            }

            .solo-btn {
                width: 13px;
                height: 13px;
                background-color: white;
                opacity: 0.3;
            }

            .solo-btn.solod {
                opacity: 1;
            }

            .mute-btn.muted {
                opacity: 0.3;
            }

            .mute-btn {
                width: 13px;
                height: 13px;
                background-color: white;
            }

            .enable-btn.enabled {
                background-color: red;
            }

            .enable-btn {
                width: 13px;
                height: 13px;
                border-radius: 13px;
                background-color: gray;
            }
        }

        .volume-box {
            height: 14px;
            line-height: 12px;
            background-color: unset;
            color: black;
            position: relative;

            .value {
                position: absolute;
                left: 5px;
            }

            .volume-indicator {
                height: 11px;
                opacity: 1;
                background-color: #f7f7f7e3 !important;
            }
        }

        .non-selected.rack-dropdown {
            .device-dropdown, .sub-device-dropdown, .parameter-dropdown {
                pointer-events: none;
                opacity: 0.2;
            }
        }

        .non-selected {
            .parameter-dropdown {
                pointer-events: none;
                opacity: 0.2;
            }
        }

        .Dropdown-menu {
            .Dropdown-option {
                font-size: 8pt;
                padding: 2px;
            }
        }

        .Dropdown-option.has-automations {
            color: red !important;
        }

        .Dropdown-control {
            background-color: #2b2a319e !important;
            color: white !important;
            border-radius: 0px;
            border: none;
            margin-bottom: 1px;
            width: 100%;
            font-size: 8pt;
            height: 15px;
            line-height: 13px;
            padding: 2px 2px 2px 2px;
            padding-top: 0px;


            .Dropdown-arrow {
                top: 5px;
                border-color: black transparent transparent !important;
            }
        }

        .track-name {
            font-size: 9pt;
            background-color: #ffffff61;
            color: black;
            padding: 0px;
            padding-left: 3px;
            margin-right: 0px;
            white-space: nowrap;
            overflow: hidden;
            width: 124px;
        }

        height: 80px;
    }
}

.sub-sequence-arrangement-container {
    z-index: 3;
    
    .sub-sequence-tracks {

        .track-time-line.selected {
            background-color: #ffffff73;
        }

        .track-time-line {
            position: relative;
            height: 80px;
            border-top-style: solid;
            border-top-width: 1px;
            border-top-color: #393737;
            border-bottom-style: solid;
            border-bottom-width: 1px;
            border-bottom-color: #393737;
            background-color: #ffffff47;
            display: flex;

            .row-selection {
                height: 78px;
                z-index: 1;
            }

            .mouse-helper {
                height: 78px;
                z-index: 0;
            }

            .track-patterns {
                display: flex;
                position: absolute;
                
                .pattern-filler  {
                    height: 80px;
                }

                .track-pattern.selected {
                    .track-step {
                        background-color: black !important;
                    }
                }

                .track-pattern {
                    height: 80px;
                    border: 1px solid #0000003d;
                    background-color: #31313194;
                    .track-pattern-header {
                        height: 15px;
                        opacity: 0.85;
                        font-size: 8pt;
                        color: black;
                        padding-left: 5px;
                        letter-spacing: 1px;
                    }

                    .track-step {
                        height: 3px !important;
                        opacity: 1 !important;
                    }
                }
            }

            .parameter-automation {
                height: 65px;
                margin-top: 15px;
            }

            svg {
                z-index: 10;
                position: absolute;
                background-color: unset;

                line {
                    z-index: 10;
                    stroke: #5d5a5a3b;
                }
                
                polyline, ellipse {
                    z-index: 10;
                    stroke: red;
                }

                ellipse.automation-point.selected {
                    stroke: red !important;
                }

                .automation-selection {
                    fill: #6a5f7f9a;
                }
            }
        }
    }
}

.arrangement-view {
    .arrangement-bottom-container {
        display: flex;
        flex-direction: column;

        .effects-view {
            flex: 1;
            background-color: #000000b0;
        }

        .piano-roll-container {
            .piano-roll-scroll {
                background-color: #3c3b42e3;
            }

            .piano-roll-control {
                .controller-container {
                    .controller {
                        background-color: #424242eb;
                        .step-control-container {
                            opacity: 1;
                        }
                    }
                }
            }
        }
        .grid-step.guide-step {
            background-color:#2121217a !important;
        }

        .piano-roll-time-row {
            background-color: #424242eb;
            color: white;
        }

        .piano-roll-row {
            background-color: #3e3e3ebf;
        }
        .piano-roll-row.black {
            background-color: #2f2e2ecc;
        }
    }
}
