$groupColor: #4a71ff;

.channel-mixer {
    ::-webkit-scrollbar-thumb {
        background-color: #525252 !important;
    }

    .btn-group {
        margin-bottom: 20px;
    }

    .groups-container {
        .effect {
            margin: 2px;
        }

        .effect.selected {
            box-shadow: unset;
        }

        .track-output-type {
            font-size: 7pt;
            margin-top: auto;

            .track-output-type-label {
                margin-left: 4px;
            }
        }

        .track-effects {
            height: 180px;
            overflow-y: scroll;
            overflow-x: hidden;
        }

        .Dropdown-control {
            margin-left: 2px;
            font-size: 8pt;
            padding: 2px;
            border-color: #1e1e22 !important;
            width: 70px;
        }

        .Dropdown-option {
            font-size: 8pt;
            padding: 2px;
            overflow-x: hidden;
        }

        .Dropdown-arrow {
            top: 6px !important;
        }

        .track.showing-effects {
            width: unset;
        }
    }

    .send-effect {
        border-radius: 3px;
        padding: 3px;
        margin: 3px;
        cursor: pointer;
        width: 68px;

        background-color: #3e5b88b3;
        color: white;

        display: flex;
        overflow: hidden;

        .effect-name {
            font-size: 8pt;
            overflow: unset;
            white-space: unset;
        }

        .parameter-visual {
            margin-top: auto;
            width: 1px;
            margin-left: 1px;
            margin-right: 1px;
            max-height: 16px;
        }
    }

    .send-effect.selected {
        box-shadow: 0px 0px 2px 2px #d3dbffbd;
        background-color: white;
        color: black;
    }
}

.groups-component {
    margin-top: auto;
    padding: 2px;

    .trigger-meter {
        width: 80px;
    }

    .mix-volume-slider {
        display: flex;

        .track-meter {
            margin-top: auto;
            border-color: #29282b;
            margin-left: auto;

            .track-meter-value {
                background-image: linear-gradient(#41f4ad, #b3ffdc);
            }

            .track-meter-reference {
                border-top: solid 1px #29282b
            }
        }

        .slider {
            height: 80px;
        }

        .slider-handle {
            background-image: linear-gradient(#eeeeef, #b2edff) !important;
        }

        margin-top: 10px;
        padding-left: 28px;

        .slider-track {
            width: 15px;
        }
        .slider-handle {
            width: 35px;
            height: 15px;
            margin-left: -5px;
        }
    }

    .group-actions {
        margin-top: auto;
        margin-left: 15px;

        .dropdown-menu.show {
            background-color: #f9f9f975;
            color: black;
            opacity: 0.95;
        }

        .Dropdown-item {
            color: black !important;
        }

        .btn-primary {
            background-color: #ffffff1c !important;
            border: none;
        }

        .group-actions-icon {
            width: 20px;
            height: 20px;
        }
    }

    .group-container {
        display: flex;
        flex-direction: column;
        margin: 1px;
        margin-right: 5px;

        .group-volume-control.showing {
            width: unset;
            display: flex;

            .send-effect {
                width: 120px;
            }
        }

        .group-volume-control {
            width: 90px;
            min-height: 280px;
            flex-direction: column;
            display: flex;
            background-color: #00000080 !important;

            .track-effects {
                margin-top: unset;
            }
        }

        .group-top {
            height: 6px;
            background-color: $groupColor;
            background-image: linear-gradient(45deg, #ffffff1c 0%, #ffffff40 21%,#ffffff7a 21%, #ffffff57 37%,#ffffff66 37%, #ffffff 46%,#ffffff94 46%, #ffffffa6 53%,#fdffff45 53%, #ffffffd4 59%,#ffffffb8 59%, #d6d6d6de 77%,#fdfdfdd6 77%, #ffffffeb 100%);
        }

        .group-info {
            display: flex;
            font-size: 10pt;
            padding-left: 3px;
            color: black;

            .collapse-toggle {
                background-color: white;
                border-color: white;
                margin-left: auto;

                .arrow-down {
                    border-top-color: black;
                }

                .arrow-right {
                    border-left-color: black;
                }
            }
        }

        .group-inner {
            display: flex;
        }
    }

    .group-track.alone {
        margin: 1px;
        margin-top: 0px;

        .track-info {
            height: 29px;
            line-height: 36px;
        }
    }

    .group-track.selected {
        .track-info {
            // background-color: white !important;
            color: black;
        }

    }

    .group-track.showing-effects {
        width: unset;
        display: flex;
        flex-direction: column;
    }

    .group-track {
        width: 85px;
        margin-right: 1px;
        background-color: #00000080;

        .track-info {
            padding-left: 3px;
            background-color: #1111117a;
            overflow: hidden;
            font-size: 9pt;
            white-space: nowrap;
            color: white;
        }

        .track-thumbnail {
            height: 40px;
            background-size: cover;
            text-align: center;
            line-height: 35px;
            letter-spacing: 2px;
            color: #0000007a;
            cursor: pointer;
        }

        .track-thumbnail.muted {
            opacity: 0.2;
            color: #110000;
        }

        .mix-volume-slider {
            margin-top: 9px;
            .slider {
                height: 80px;
            }

            padding-left: 28px;
        }

        .track-controls {
            padding-top: 5px;
            display: block;
            margin-left: 0px;
            background-color: #00000080;

            .pan-knob {
                margin-left: 25px;
            }

            .sends-label {
                padding-left: 7px;
                font-size: 7pt;
            }

            .send-knobs {
                display: flex;
                padding-left: 5px;
                flex-wrap: wrap;
                .send-knob {
                    height: 40px;
                    .send-name {
                        font-size: 6pt;
                        text-align: center;
                        margin-bottom: unset;
                    }
                }
            }
        }
    }

}

.mix-effects-pane {
    display: flex;
    flex-direction: column;

    .mix-effects-header {
        width: 100%;
        height: 20px;
        color: black;
        background-image: linear-gradient(45deg, transparent, #ffffffc9);
        display: flex;

        .envelope-options {
            .envelope-option {
                color: black;
                background-color: #0000004f;
            }
            .envelope-option.connected {
                color: black;
                background-color: white;
            }
        }

        .element-name {
            background-color: #ffffff73;
            color: #000000;
            line-height: 15px;
            height: 15px;
            margin: 5px;
            font-size: 9pt;
            padding-left: 5px;
            padding-right: 5px;
            width: 200px;
        }
    }

    .mix-effects-inner {
        display: flex;
        flex: 1;
        background-color: #000000b0;
    }
    width: 100%;
    height: 280px;

    .audio-effects {
        display: flex;
    }

    .effect-option {
    }

    .effect.selected {
        box-shadow: 0px 0px 1px 1px white;
    }
    .effect {
        margin-right: 0px;
        margin-left: 0px;
        margin-bottom: 0px;
        color: white !important;


        .amp-option {
            color: white;
        }
    }

    .effect.midi.enabled, .effect.enabled {
        background-color: #b6ffe12e;
        .effect-header {
            background-color: #b6ffe12e;
        }

        .bottom-effect-toolbar {
            background-color: #1434a840;
        }
    }

    .effect.midi.enabled {
        .bottom-effect-toolbar {
            background-color: #7effb242;
        }
    }

    .effects-channel-strip {
        padding: 2px;
        background-image: linear-gradient(45deg, #a99797, transparent);
        width: 140px;

        .element-name {
            font-size: 9pt;
            white-space: nowrap;
            overflow: hidden;
            color: black;
            padding-left: 3px;
            margin-bottom: 2px;
        }

        .effects-list {
            margin-top: 5px;
            width: 140px;
            max-height: 220px;
            overflow-y: scroll;
            ::-webkit-scrollbar-thumb {
                background-color: white !important;
                opacity: 0.2;
            }
        }

        .send-effects-dropdown {
            font-size: 8pt;

            .Dropdown-option {
                background-color: white !important;
                color: black !important;
            }

            .Dropdown-option:hover {
                background-color: #dedede !important;
            }

            .Dropdown-control {
                height: 20px;
                width: 105px !important;
                line-height: 5px;
                background-color: #ffffff85 !important;
                color: black !important;
            }

            .Dropdown-arrow {
                top: 7px;
            }
        }
    }

    .contextual-help {
        margin-left: auto;
        margin-top: auto;
        border-color: #ececec !important;
        background-color: #00000014;

        .key.mini {
            border-color: #d6d6d6;
        }
    }
}

.mix-view.mixes {
    position: relative;

    .track.sends, .scenes-component, .pitch-clock {
        backdrop-filter: blur(4px);
    }

    .grids {
        margin-top: 100px;
        /*
        background-image: radial-gradient(circle at center center, transparent 0%, #21212108 99%), repeating-linear-gradient(
            0deg
            , #a3a3a305 0px, #a3a3a305 1px, transparent 1px, transparent 6px), repeating-linear-gradient(
            90deg
            , #a3a3a300 0px, #a3a3a30d 1px, transparent 1px, transparent 6px), linear-gradient(
            90deg
            , #21212105, #21212117);
        */

        .grids-name {
            position: relative;
            background-color: #0a0a0acf;
            padding: 5px;
            padding-right: 50px;
        }
    }
    .steps-visualizer {
        padding-bottom: 100px;
    }

    .channel-mixer {
        //width: unset;
        position: fixed;
        bottom: 0px;
        z-index: 2500000000000;

    }
}

.mix-view {
    /*background-image: linear-gradient(90deg, #00020e 0%, transparent 59%), repeating-linear-gradient(45deg, rgba(168, 168, 168, 0.1) 0px, rgba(168, 168, 168, 0.1) 1px, transparent 1px, transparent 13px), repeating-linear-gradient(135deg, rgba(168, 168, 168, 0.1) 0px, rgba(168, 168, 168, 0.1) 1px, transparent 1px, transparent 13px), linear-gradient(90deg, #01000f, #00010be8);*/
    overflow: scroll;

    .grids-box {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    .grids-container {
        display: inline-flex;
        flex: 1;

        .grids.collapsed {
            .grids-inner {
                margin-top: unset;
                min-width: unset;
                min-height: unset;
            }
        }
        .grids {
            margin: 10px;
            display: flex;
            flex-direction: column;
            align-items: flex-start;

            .grids-inner {
                min-width: 600px;
                margin-top: 45px;
                min-height: 600px;
                background-image: radial-gradient(circle at center center, #d5d8d959 0%, #3c3c3c5c 12%, transparent 12%, transparent 100%), linear-gradient(
90deg, #fbfdfe00, #fbfdfe00);
                background-size: 10px 10px;

                display: flex;
                flex-wrap: wrap;
                position: relative;
                border: 1px dashed #7d7d7d73;
                background-color: #17171773;
            }

            .context-menu {
            }

            .grid-rows {
                display: flex;
                flex-direction: column;
            }
           
            .grid.pressed {
                background-color: #6579c3;
            }

            .grid {
                margin: 10px;
                width: 60px;
                height: 60px;
                background-color: #5a5a5a;

                .volume-indicator {
                    width: 60px;
                }
            }
        }
    }
    }

    .sends.track {
        margin-left: auto;
        background-color: #1d1d1d87;

        .sends-container {
            .send-effect {
                background-color: #3e5b88b3;;
            }
            .send-effect.selected {
                background-color: white !important;
            }
        }

        .track-header {
        }
    }
}

.tracks-and-tracker {
    .group-tracks {
        display: flex;
    }

}

